@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/helpers/others' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.details-page-wrapper {
  padding: 6px 20px;

  .grid-container {
    display: grid;
    grid-template-columns: 1.1fr 0.9fr;
    grid-template-rows: 480px 0.2fr 0.5fr;
    column-gap: 20px;

    .list-container {
      grid-row: span 3;
    }

    .tabs-container {
      .tabs {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 16px;

        .tab {
          text-align: center;
          @extend .shadow-normal;
          padding: 8px 16px;
          font-size: 14px;
          color: pick-color('primary', 'web');
          border-radius: 6px;
          flex-grow: 1;
          background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 37%);
          border: 1px solid pick-color('primary', 'fog');
          cursor: pointer;

          &.active-tab {
            color: white;
            background-color: pick-color('primary', 'web');
          }
        }
      }

      .tab-content-wrapper {
        padding: 20px;
        border: 1px solid pick-color('primary', 'fog');
        background-color: white;
        border-radius: 8px;
      }
    }
  }
}
